import uCharts from '../../js_sdk/u-charts/u-charts/u-charts.min.js';
import apiConfigs from '../../static/api/index.js'
var _self;
var canvaRadar=null;
export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			textarea:'',
			radar:{
				categories:['体重kg','骨骼肌kg','体肪脂kg','身体水分含量kg','去脂体重kg'],
				series:[{
					name:'',
					data:[46.06,40.06,13.06,39.09,39.09]
				}]
			}
		}
	},
	onLoad() {
		_self = this;











		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.radarInit();
	},
	methods: {
		navBack(){
			uni.navigateTo({
				url:'myLogin'
			})
		},
		radarInit(){
			this.showRadar("canvasRadar",this.radar)
		},
		showRadar(canvasId,chartData){
			canvaRadar=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'radar',
				fontSize:11,
				padding:[0,0,-30,-20],
				fontColor:'#fff',
				legend:{
					show:false,
					padding:5,
					lineHeight:11,
					margin:0,
				},
				background:'#fff',
				pixelRatio:_self.pixelRatio,
				animation: true,
				dataLabel: true,
				categories: chartData.categories,
				series: chartData.series,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					radar: {
						max: 200//雷达数值的最大值
					}
				}
			});
		},
		touchRadar(e){
			canvaRadar.touchLegend(e);
			canvaRadar.showToolTip(e, {
				format: function (item) {
					return item.name + ':' + item.data 
				}
			});
		},
		getInfo(){
			let that = this;
			uni.request({
				url:ANGLE_instanced_arrays
			})
		}
	}
}
